import React, { Component } from "react";
import PropTypes from "prop-types";
import style from "./SearchCalc.module.css";
import departures from "../../assets/data/places.json";
import prices from "../../assets/data/comissionPrice.json";

class SearchCalc extends Component {
  state = {
    isMediumCustomValue: false,
    selectedAuction: "",
    lotPrice: "0",
    deliverySea: "900",
    overlandDeliveryCost: "0",
    company–°ommission: 900,
    deaprturePort: "",
    aucComission: "0",
    insurance: "0",
    portExpedition: "450",
    brokerPrice: "400",
    evacution: "250",
    certification: "200",
    importDuty: "0",
    nds: "0",
    exise: "0",
    esv: "0",
    car: {},
    accounting: "45"
  };

  static propTypes = {
    selectedAuction: PropTypes.string,
    lotPrice: PropTypes.number,
    car: PropTypes.shape({
      location: PropTypes.string.isRequired,
      branchname: PropTypes.string
    })
  };

  static defaultProps = {
    selectedAuction: "",
    lotPrice: "1000",
    car: { branchname: "" }
  };

  componentDidMount() {
    const { selectedAuction, lotPrice, car } = this.props;

    this.setState(
      {
        selectedAuction,
        lotPrice,
        insurance: Math.round(Number(lotPrice * 0.04)),
        car: { ...car }
      },
      () => {
        this.deaprtureFinder();

        this.taxesCalc();
      }
    );
  }

  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      const { selectedAuction, lotPrice, car } = this.props;

      this.setState(
        {
          selectedAuction,
          lotPrice,
          insurance: Math.round(Number(lotPrice * 0.04)),
          car: { ...car }
        },
        () => {
          this.deaprtureFinder();
          this.comissionCalc();
          this.taxesCalc();
        }
      );
    }
  }

  deaprtureFinder = () => {
    const { car } = this.props;
    const city = car.location ? car.location.substr(5) : car.branchname;
    const departurePlace = departures.find(el => {
      return el.city.toLocaleLowerCase() === city.toLocaleLowerCase();
    });

    if (departurePlace) {
      const sortedDeparturePlaces = Object.values(departurePlace.land).sort(
        (a, b) => {
          return a.amount - b.amount;
        }
      );

      this.setState(
        {
          deaprturePort: sortedDeparturePlaces[0].name,
          overlandDeliveryCost: sortedDeparturePlaces[0].amount
        },

        () => {
          this.comissionCalc();
        }
      );
      return;
    }
    this.setState(
      {
        overlandDeliveryCost: 350
      },
      () => {
        this.comissionCalc();
      }
    );
  };

  comissionCalc = () => {
    const { lotPrice, selectedAuction } = this.state;
    let aucComission = "";
    let bidFee = "";
    if (selectedAuction === "copart") {
      const comissionArray = prices.CopartArray[0].comission;
      const bidFeeArray = prices.CopartArray[1].bidFee;
      const { gateFee } = prices.CopartArray[2];

      comissionArray.find((el, index) => {
        if (el[0] > Number(lotPrice)) {
          aucComission = el[1];
          return aucComission;
        }
        if (comissionArray.length - 1 === index && el[0] <= lotPrice) {
          aucComission = Math.round(Number(lotPrice) * 0.04);

          return aucComission;
        }
      });
      bidFeeArray.find(el => {
        if (el[0] > lotPrice) {
          bidFee = el[1];
          return bidFee;
        }
      });

      const totalAucComission =
        Number(bidFee) + Number(aucComission) + Number(gateFee);
      const totalCarPrice = Number(lotPrice) + Number(totalAucComission);
      // console.log(bidFee);
      // console.log(aucComission);
      // console.log(gateFee);
      this.setState(
        {
          aucComission: Math.round(
            Number(gateFee) + Number(bidFee) + Number(aucComission)
          ),
          importDuty: Math.round(totalCarPrice * 0.1)
        },
        () => {
          this.taxesCalc();
        }
      );
    }

    if (selectedAuction === "iaai") {
      const comissionArray = prices.IaaiArray[0].comission;
      const bidFeeArray = prices.IaaiArray[1].bidFee;
      const { gateFee } = prices.IaaiArray[2];

      comissionArray.find((el, index) => {
        if (el[0] > Number(lotPrice)) {
          if (Number(lotPrice) > 7499 && Number(lotPrice) < 20000) {
            aucComission = el[1] + Number(lotPrice) * 0.01;
            return aucComission;
          }
          aucComission = el[1];
          return aucComission;
        }

        if (comissionArray.length - 1 === index && el[0] <= lotPrice) {
          aucComission = Math.round(Number(lotPrice) * 0.04);
          return aucComission;
        }
      });
      bidFeeArray.find(el => {
        if (el[0] > lotPrice) {
          bidFee = el[1];
          return bidFee;
        }
      });
      const totalAucComission =
        Number(bidFee) + Number(aucComission) + Number(gateFee);
      const totalCarPrice = Number(lotPrice) + Number(totalAucComission);
      // console.log(bidFee);
      // console.log(aucComission);
      // console.log(gateFee);
      this.setState(
        {
          aucComission: Math.round(Number(totalAucComission)),
          importDuty: Math.round(totalCarPrice * 0.1)
        },
        () => {
          this.taxesCalc();
        }
      );
    }
  };

  taxesCalc = () => {
    const { lotPrice, aucComission, importDuty, car } = this.state;

    const carVol = car.vol || (car.engine && car.engine.slice(0, 3));

    if (car.year > 1 && carVol >= 0) {
      let coeficient = 50;
      const ageOfCar = 2020 - Number(car.year);
      let exise = "";
      let totalCustomPrice = "";
      let nds = "";

      switch (car.fuel) {
        case "GAS":
        case "Gasoline":
        case "FLEXIBLE FUEL":
          if (Number(carVol) > 3.5) {
            coeficient = 100;
          }
          exise = Math.round(
            Number(ageOfCar) * Number(carVol) * Number(coeficient)
          );
          totalCustomPrice =
            Number(lotPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);

          break;

        case "DIESEL":
          if (Number(carVol) > 3.5) {
            coeficient = 150;
          } else coeficient = 75;
          exise = Math.round(
            Number(ageOfCar) * Number(carVol) * Number(coeficient)
          );
          totalCustomPrice =
            Number(lotPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          break;
        case "HYBRID ENGINE":
          exise = 100;
          totalCustomPrice =
            /* */ Number(lotPrice) +
            Number(aucComission) +
            /* */ Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          break;
        case "ELECTRIC":
          exise = 0;
          totalCustomPrice = 0;
          nds = 0;
          break;
        default:
          if (Number(carVol) > 3.5) {
            coeficient = 100;
          }
          exise = Math.round(
            Number(ageOfCar) * Number(carVol || 2.0) * Number(coeficient)
          );
          totalCustomPrice =
            Number(lotPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          this.setState({ isMediumCustomValue: true });
          break;
      }

      const esv = Math.round(totalCustomPrice * 0.04);
      this.setState({ nds, exise, esv });
    }
  };

  render() {
    const {
      lotPrice,
      deliverySea,
      overlandDeliveryCost,
      company–°ommission,
      aucComission,
      deaprturePort,
      insurance,
      portExpedition,
      brokerPrice,
      evacution,
      importDuty,
      exise,
      esv,
      nds,
      certification,
      accounting,
      isMediumCustomValue
    } = this.state;
    const totalDelivery =
      Number(lotPrice) +
      Number(deliverySea) +
      Number(aucComission) +
      Number(insurance) +
      Number(company–°ommission) +
      Number(overlandDeliveryCost);
    const totalCustom =
      Number(portExpedition) +
      Number(brokerPrice) +
      Number(importDuty) +
      Number(exise) +
      Number(nds) +
      Number(esv) +
      Number(evacution) +
      Number(certification) +
      Number(accounting);

    const totalPrice = totalDelivery + totalCustom;

    return (
      <>
        <div className={style.outWrapper}>
          <div className={style.container}>
            <h2 className={style.title}>–û–ø–ª–∞—Ç–∞ –ø—ñ—Å–ª—è –ø–æ–∫—É–ø–∫–∏</h2>
            <div className={style.deliveryWrapper}>
              <span className={style.span}>
                –¶—ñ–Ω–∞ –ª–æ—Ç–∞:
                <span className={style.innerSpan}> {lotPrice}$</span>
              </span>

              <span className={style.span}>
                –ö–æ–º—ñ—Å—ñ—è –∞—É–∫—Ü—ñ–æ–Ω—É:
                <span className={style.innerSpan}>{aucComission}$</span>
              </span>

              <span className={style.span}>
                –î–æ—Å—Ç–∞–≤–∫–∞ –ø–æ –°–®–ê –≤ –ø–æ—Ä—Ç {deaprturePort}
                <span className={style.innerSpan}>
                  {overlandDeliveryCost === "0"
                    ? `–£–≤–∞–≥–∞!!!0`
                    : overlandDeliveryCost}
                  $
                </span>
              </span>

              <span className={style.span}>
                –¶—ñ–Ω–∞ –¥–æ—Å—Ç–∞–≤–∫–∏ –º–æ—Ä–µ–º:
                <span className={style.innerSpan}>{deliverySea}$</span>
              </span>

              <span className={style.span}>
                –°—Ç—Ä–∞—Ö–æ–≤–∫–∞:
                <span className={style.innerSpan}>{insurance}$</span>
              </span>

              <span className={style.span}>
                –ü–æ—Ä—Ç –¥–æ—Å—Ç–∞–≤–∫–∏:
                <span className={style.innerSpan}>–û–¥–µ—Å–∞</span>
              </span>

              <span className={style.span}>
                –°—Ä–æ–∫ –¥–æ—Å—Ç–∞–≤–∫–∏:
                <span className={style.innerSpan}>55 –¥–Ω—ñ–≤</span>
              </span>

              <span className={style.span}>
                –ö–æ–º—ñ—Å—ñ—è –∫–æ–º–ø–∞–Ω—ñ—ó:
                <span className={style.innerSpan}>{900}$</span>
              </span>

              <span className={style.totalDeliveryCostWrappe}>
                <span className={style.totalDeliveryCost}>
                  –ó–∞–≥–∞–ª—å–Ω–∞ —Å—É–º–∞ –¥–æ –ø–æ—Ä—Ç—É –û–¥–µ—Å–∞:
                  <span className={style.innerSpan}>{totalDelivery}$ </span>
                </span>
              </span>
            </div>
          </div>
          <div className={style.container}>
            <h2 className={style.title}>–û–ø–ª–∞—Ç–∞ –ø–æ –ø—Ä–∏–±—É—Ç—Ç—é</h2>
            <span className={style.span}>
              –ï–∫—Å–ø–µ–¥–∏—Ü—ñ—è –≤ –ø–æ—Ä—Ç—É:
              <span className={style.innerSpan}>{portExpedition}$</span>
            </span>
            <span className={style.span}>
              –ë—Ä–æ–∫–µ—Ä:<span className={style.innerSpan}>{brokerPrice}$</span>
            </span>
            <span className={style.span}>
              –í–≤—ñ–∑–Ω–µ –º–∏—Ç–æ:
              <span className={style.innerSpan}>{importDuty}$</span>
            </span>
            <span className={style.span}>
              –ê–∫—Ü–∏–∑–Ω–∏–π –∑–±—ñ—Ä:
              <span className={style.innerSpan}>
                {isMediumCustomValue && "*"}
                {exise}$
              </span>
            </span>
            <span className={style.span}>
              –ü–î–í:
              <span className={style.innerSpan}>
                {isMediumCustomValue && "*"} {nds}$
              </span>
            </span>
            <span className={style.span}>
              –ü–µ–Ω—Å—ñ–π–Ω–∏–π —Ñ–æ–Ω–¥:
              <span className={style.innerSpan}>
                {isMediumCustomValue && "*"}
                {esv}$
              </span>
            </span>
            <span className={style.span}>
              –ï–≤–∞–∫—É–∞—Ç–æ—Ä –¥–æ –∞–¥—Ä–µ—Å–∏ –∫–ª—ñ—î–Ω—Ç–∞:
              <span className={style.innerSpan}>{evacution}$</span>
            </span>
            <span className={style.span}>
              –°–µ—Ä—Ç–∏—Ñ—ñ–∫–∞—Ü—ñ—è:
              <span className={style.innerSpan}>{certification}$</span>
            </span>
            <span className={style.span}>
              –ü–æ—Å—Ç–∞–Ω–æ–≤–∫–∞ –Ω–∞ –æ–±–ª—ñ–∫:
              <span className={style.innerSpan}>{accounting}$</span>
            </span>
            <span className={style.totalCustomCost}>
              –°—É–º–∞ –ø–ª–∞—Ç–µ–∂—ñ–≤ –≤ –£–∫—Ä–∞—ó–Ω—ñ:
              <span className={style.innerSpan}> {totalCustom}$</span>
            </span>
            <span className={style.totalCost}>
              –ó–∞–≥–∞–ª—å–Ω–∞ –≤–∞—Ä—Ç—ñ—Å—Ç—å:
              <span className={style.innerSpan}> {totalPrice}$</span>
            </span>
            {isMediumCustomValue && (
              <span>
                // eslint-disable-next-line react/no-unescaped-entities *-
                –ó–Ω–∞—á–µ–Ω–Ω—è –æ–±—á–∏—Å–ª–µ–Ω—ñ –¥–ª—è –±–µ–Ω–∑–∏–Ω–æ–≤–æ–≥–æ –¥–≤–∏–≥—É–Ω–∞ –æ–±'—î–º–æ–º 2.0–ª.
              </span>
            )}
          </div>
        </div>
      </>
    );
  }
}

export default SearchCalc;
