import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";
import style from "./OrderForm.module.css";
import "react-notifications/lib/notifications.css";
import * as API from "../../api/api";
import Loader from "../Loader/Loader";

class OrderForm extends Component {
  state = {
    name: "",
    phone: "",
    comment: "",
    isLoading: false
  };

  static propTypes = {
    title: PropTypes.string,
    value: PropTypes.string
  };

  static defaultProps = {
    title: "",
    value: ""
  };

  abortLoading = () => {
    this.setState({
      isLoading: false
    });
  };

  handleChange = ({ target }) => {
    this.setState({ [target.id]: target.value });
  };

  submitForm = e => {
    e.preventDefault();

    const { name, phone, comment } = this.state;
    if (name.length < 3 || phone.length < 7) {
      NotificationManager.error(
        "–ù–µ –∫–æ—Ä–µ–∫—Ç–Ω–µ —ñ–º'—è –∞–±–æ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!",
        "–ü–æ–º–∏–ª–∫–∞",
        3000
      );
      return;
    }
    this.setState({ isLoading: true });

    API.sendMessageTelegram(
      `–Ü–º—è: ${name}, –¢–µ–ª–µ—Ñ–æ–Ω: ${phone}, –ö–æ–º–µ–Ω—Ç–∞—Ä: ${comment}`
    )
      .then(res => {
        return res.data.ok ? (
          (NotificationManager.success(
            "–î—è–∫—É—é–º–æ –∑–∞ –∑–≤–µ—Ä–Ω–µ–Ω–Ω—è, –º–∏ –∑–≤'—è–∂–µ–º–æ—Å—å –∑ –í–∞–º–∏ –Ω–∞–π–±–ª–∏–∂—á–∏–º —á–∞—Å–æ–º.",
            "–£—Å–ø—ñ—à–Ω–æ"
          ),
          this.setState({ isLoading: false, name: "", phone: "", comment: "" }))
        ) : (
          <span>–©–æ—Å—å –ø—ñ—à–ª–æ –Ω–µ —Ç–∞–∫. –°–ø—Ä–æ–±—É–π—Ç–µ —â–µ —Ä–∞–∑</span>
        );
      })
      .catch(err => {
        // eslint-disable-next-line no-console
        console.log(err);
      });
  };

  render() {
    const { title, value } = this.props;
    const { name, phone, comment, isLoading } = this.state;
    return (
      <>
        {isLoading && <Loader abortLoading={this.abortLoading} />}
        <div className={style.container}>
          <div className={style.shadow}>
            <h2 className={style.title}>
              {title || "–ó–∞–º–æ–≤ –∞–≤—Ç–æ –∑–∞—Ä–∞–∑ —Ç–∞ –∑–∞–æ—â–∞–¥—å –¥–æ 50%"}
            </h2>

            <form className={style.orderForm}>
              <input
                type="text"
                id="name"
                className={style.inputName}
                onChange={this.handleChange}
                value={name}
                placeholder="–í–∞—à–µ —ñ–º'—è*"
              />
              <input
                type="number"
                id="phone"
                className={style.inputNumber}
                onChange={this.handleChange}
                value={phone}
                placeholder="–í–∞—à —Ç–µ–ª–µ—Ñ–æ–Ω*"
              />
              <textarea
                type="text"
                id="comment"
                value={comment}
                className={style.inputComment}
                onChange={this.handleChange}
                placeholder="–ö–æ–º–µ–Ω—Ç–∞—Ä"
              />
              <input
                type="submit"
                value={value || "–ó–∞–º–æ–≤–∏—Ç–∏ –∞–≤—Ç–æ"}
                className={style.submitBtn}
                onClick={this.submitForm}
              />
            </form>
          </div>
          <NotificationContainer />
        </div>
      </>
    );
  }
}

export default OrderForm;
