import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";
import * as API from "../../api/api";
import Loader from "../Loader/Loader";
import style from "./CallBack.module.css";
import "react-notifications/lib/notifications.css";

class CallBackModal extends Component {
  state = {
    name: "",
    phone: "",
    comment: "",
    isLoading: false
  };

  static propTypes = {
    toggleModal: PropTypes.func.isRequired,
    carText: PropTypes.string
  };

  static defaultProps = {
    carText: ""
  };

  componentDidMount() {
    const { carText } = this.props;
    this.setState({ comment: carText });
    document.body.style = " overflow: hidden ";
  }

  componentWillUnmount() {
    document.body.style = "";
  }

  abortLoading = () => {
    this.setState({
      isLoading: false
    });
  };

  handleChange = ({ target }) => {
    this.setState({ [target.id]: target.value });
  };

  formSubmit = e => {
    e.preventDefault();
    const { toggleModal } = this.props;
    const { name, phone, comment } = this.state;
    if (name.length < 3) {
      NotificationManager.error(
        "–ù–µ –∫–æ—Ä–µ–∫—Ç–Ω–µ —ñ–º'—è! M—ñ–Ω—ñ–º—É–º 3 —Å–∏–º–≤–æ–ª–∏ ",
        "–ü–æ–º–∏–ª–∫–∞",
        3000
      );
      return;
    }
    if (phone.length < 7) {
      NotificationManager.error(
        "–ù–µ –∫–æ—Ä–µ–∫—Ç–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!",
        "–ü–æ–º–∏–ª–∫–∞",
        3000
      );

      return;
    }
    this.setState({ isLoading: true });

    API.sendMessageTelegram(
      `–Ü–º—è: ${name}, –¢–µ–ª–µ—Ñ–æ–Ω: ${phone}, –ö–æ–º–µ–Ω—Ç–∞—Ä: ${comment}`
    )
      .then(res => {
        return res.data.ok ? (
          (NotificationManager.success(
            "–î—è–∫—É—é–º–æ –∑–∞ –∑–≤–µ—Ä–Ω–µ–Ω–Ω—è, –º–∏ –∑–≤'—è–∂–µ–º–æ—Å—å –∑ –í–∞–º–∏ –Ω–∞–π–±–ª–∏–∂—á–∏–º —á–∞—Å–æ–º.",
            "–£—Å–ø—ñ—à–Ω–æ"
          ),
          this.setState(
            { isLoading: false, name: "", phone: "", comment: "" },
            () => toggleModal()
          ))
        ) : (
          <span>–©–æ—Å—å –ø—ñ—à–ª–æ –Ω–µ —Ç–∞–∫. –°–ø—Ä–æ–±—É–π—Ç–µ —â–µ —Ä–∞–∑</span>
        );
      })
      .catch(err => {
        // eslint-disable-next-line no-console
        console.log(err);
      });
  };

  render() {
    const { name, phone, comment, isLoading } = this.state;
    const { toggleModal, carText } = this.props;
    return (
      <>
        {isLoading && <Loader abortLoading={this.abortLoading} />}
        <div className={style.overlay}>
          <div className={style.container}>
            <h3 className={style.title}>
              –ó–∞–ª–∏—à—Ç–µ –í–∞—à—ñ –∫–æ–Ω—Ç–∞–∫—Ç–Ω—ñ –¥–∞–Ω—ñ —ñ –Ω–∞—à –º–µ–Ω–µ–¥–∂–µ—Ä –∑–≤—è–∂–µ—Ç—å—Å—è –∑ –≤–∞–º–∏
              –ø—Ä–æ—Ç—è–≥–æ–º 30—Ö–≤
            </h3>
            <div className={style.formWrappe}>
              <form onSubmit={this.formSubmit}>
                <label htmlFor="phoneNumber">
                  –ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É:*
                  <input
                    type="number"
                    id="phone"
                    className={style.input}
                    value={phone}
                    onChange={this.handleChange}
                  />
                </label>
                <label htmlFor="nameInput">
                  –í–∞—à–µ —ñ–º—è:*
                  <input
                    type="text"
                    value={name}
                    className={style.input}
                    id="name"
                    onChange={this.handleChange}
                  />
                </label>
                <label htmlFor="comment">
                  –ö–æ–º–µ–Ω—Ç–∞—Ä:
                  <textarea
                    className={style.textArea}
                    value={carText || comment || ""}
                    id="comment"
                    onChange={this.handleChange}
                  />
                </label>
              </form>
              <button
                type="button"
                onClick={toggleModal}
                className={style.closeButton}
              />
            </div>
            <button
              className={style.CallBackSubmitButton}
              onClick={this.formSubmit}
              type="button"
            >
              –ó–∞–ª–∏—à–∏—Ç–∏ –∑–∞—è–≤–∫—É
            </button>
          </div>
          <NotificationContainer />
        </div>
      </>
    );
  }
}

export default CallBackModal;
